.main {
    width: 100%;
    height: 100vh;
    display: flex;
    justify-content: center;
    align-items: center;
    background-color: rgb(228, 215, 215);
   
}

.inner {
    width: 500px;
    max-width: 700px;
    height: 90%;
    display: flex;
    justify-content: center;
    align-items: center;
    background-color: rgb(151, 216, 222);
    flex-direction: column;
    text-align: center;
    border-radius: 4px;
    box-shadow: 3px 6px 23px;
}

input {
    display: flex;
    justify-content: center;
    align-items: center;
    position: relative;
    top: -260px;
    left: -22px;
    padding: 12px 12px;
    border-radius: 6px;
    width: 80%;
    text-transform: capitalize;
    letter-spacing: 1px;
    font-size: 18px;
    margin-bottom: 12px;
    border: none;
}

.btn {
    position: relative;
    top: -315px;
    left: 150px;
    width: 50px;
    height: 40px;
    font-size: 30px;
    border-radius: 15px;
  border: none;
  background-color: #fff;
   
}

.inner ol {
    display: block;
    /* background-color: #fff; */
    position: absolute;
    width: 300px;
    height: 70%;
}

.list_div {

    position: relative;
    left: -80px;
    display: flex;
    padding: 12px;
    /* border: 1px solid; */
    width:500px;
    height:6vh;
    flex-wrap: wrap;
    overflow-y: auto;
    overflow: hidden;
}

.list_div li {
    font-size: 17px;
    text-transform: capitalize;
    margin-right: 14px;
    text-decoration: none;
}

.list_div span {
    font-size: 20px;
    text-transform: capitalize;
    margin-right: 14px;
}

.delte {
    color: red;
    font-size: 30px;
}

.edit {
    color: rgb(23, 122, 17);
}